import React from 'react';
import SVG from '../assets/SVG'
import SocialIcon from './SocialIcon';

const SocialBar = () => (
  <div style={{
    display: 'flex',
    justifyContent: 'center',
    width: '100%',
  }} >
    <SocialIcon url="https://github.com/cjhines/" label="GitHub" data={SVG.GITHUB} />
    <SocialIcon url="https://www.linkedin.com/in/chris-hines-b9a048190/" label="LinkedIn" data={SVG.LINKEDIN} />
    {/* Cᴏᴘɪᴇᴅ ᴛᴏ ᴄʟɪᴘʙᴏᴀʀᴅ ! */}
    <SocialIcon url="mailto:chrishinesabdn@gmail.com" label="Email" data={SVG.EMAIL} />
  </div>
)

export default SocialBar;
